html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

a {
  text-decoration: none;
  line-height: 1.6;
}

img {
  max-width: 100%;
  width: auto;
  height: auto;
}

h1 {
  margin: 0 0 2.5rem;
  line-height: 1.5;
}

h2 {
  margin: 0 0 2.5rem;
  line-height: 1.5;
}

h3 {
  margin: 0 0 2.5rem;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

p {
  font-size: 2rem;
  color: #111;
}

[class$=__contenedor] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem 0;
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  margin-bottom: 3rem;
  width: 30rem;
  color: #e8505b;
  font-weight: bold;
}
@media (min-width: 1024px) {
  .header__logo {
    margin-bottom: 0;
  }
}
.header__logo-texto {
  text-align: center;
}

@media (min-width: 768px) {
  .hero__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.hero__contenido {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5rem;
  background-color: #e8505b;
  border-radius: 1rem;
}
.hero__picture {
  display: flex;
  justify-content: center;
  margin-top: 5rem;
}
.hero__img {
  width: 30rem;
}
@media (min-width: 768px) {
  .hero__img {
    width: 90%;
  }
}

.form__titulo {
  text-align: center;
  color: #fff;
}
.form__camp {
  margin-bottom: 1rem;
}
.form__label {
  display: block;
  margin: 1rem 1rem;
  text-transform: uppercase;
  text-align: left;
  color: #fff;
}
.form__input {
  width: 100%;
  padding: 1rem;
  outline: none;
  border: none;
  border-radius: 1rem;
}
.form__btn {
  display: block;
  padding: 1rem 2rem;
  width: 100%;
  background-color: #455a64;
  font-size: 2rem;
  font-weight: 700;
  color: #fff;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  border-radius: 1rem;
  margin-top: 2rem;
  border: none;
}
@media (min-width: 768px) {
  .form__btn {
    width: auto;
  }
}
@media (hover: hover) {
  .form__btn:hover {
    background-color: #303f46;
    cursor: pointer;
  }
}
@media (min-width: 768px) {
  .form__btn {
    width: 100%;
  }
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  width: 100%;
}
@media (min-width: 1024px) {
  .nav {
    width: auto;
    flex-direction: row;
  }
}
.nav__link {
  font-size: 2rem;
  font-weight: 700;
  color: #111;
  border-bottom: 0.5rem solid transparent;
  transition: 0.2s ease-in-out;
}
@media (hover: hover) {
  .nav__link:hover {
    border-bottom: 0.5rem solid #e8505b;
  }
}
.nav__link--register {
  display: block;
  padding: 1rem 2rem;
  width: 100%;
  background-color: #e8505b;
  font-size: 2rem;
  font-weight: 700;
  color: #fff;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .nav__link--register {
    width: auto;
  }
}
@media (hover: hover) {
  .nav__link--register:hover {
    background-color: #e22331;
    cursor: pointer;
  }
}